@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue','Rubik',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

header.MuiPaper-root.MuiAppBar-root.MuiAppBar-positionStatic.MuiAppBar-colorPrimary.MuiPaper-elevation4 {
  background-color: white;
}
h6.MuiTypography-root.Brand.MuiTypography-h6 {
  color: #222d67;
  font-size: 1.12rem;
  font-family: "Rubik";
  font-weight: 500;
}

.HeaderItems {
  display: flex;
  margin-left: auto;
  color: black;
}

h6.MuiTypography-root.items.MuiTypography-h6 {
  font-size: 1.12rem;
  font-family: "Rubik";
  padding: 12px;
  color: #222d67;
}
h6.MuiTypography-root.items.MuiTypography-h6:hover {
    background-color: rgba(0, 0, 0, 0.04);
    
}

@media (min-width: 600px) {
  .MuiToolbar-root.MuiToolbar-regular.MuiToolbar-gutters {
    height: 83px;
  }
}
@media only screen and (min-width: 425px) {
  .MuiToolbar-regular {
    min-height: 40%;
  }
}
@media only screen and (min-width: 768px) {
  .MuiToolbar-regular {
    min-height: 50%;
  }
}
@media only screen and (min-width: 1024px) {
  .MuiToolbar-regular {
    min-height: 35%;
  }
}
@media only screen and (min-width: 1440px) {
  .MuiToolbar-regular {
    min-height: 25%;
  }
}

.StatusCards {
  justify-content: center;
  display: flex;
  margin-top: 2rem;
}
.MuiPaper-root.MuiCard-root.StatusList.MuiPaper-elevation1.MuiPaper-rounded {
  margin: 10px;
  width: 6rem;
  height: 6rem;
  background: #d5defd;
  color: #127cd4;
  
}
.active, .MuiPaper-root.MuiCard-root.StatusList.MuiPaper-elevation1.MuiPaper-rounded:hover {
  background-color: #127cd4;
  color: white;
  
}
p.MuiTypography-root.Status_TYPE.MuiTypography-body1{
  font-size: 1rem;
  font-family: "Rubik";
  
  
}
p.MuiTypography-root.Status_COUNT.MuiTypography-body1 {
  font-family: "Rubik";
  float: right;
  font-size: xx-large;
  
}

